import React, { Component } from 'react';
import './EditProfile.scss';
import '../../index.css';
import Pencil from 'react-icons/lib/fa/pencil';
import ExternalLink from 'react-icons/lib/fa/external-link-square';
import Delete from 'react-icons/lib/ti/delete';
import Check from 'react-icons/lib/fa/check';
import Add from 'react-icons/lib/md/add-circle';
import Dropzone from 'react-dropzone';
import axios from 'axios';
import Footer from '../../components/Footer/Footer';
import Navbar from '../../components/Navbars/StudentNavbar/StudentNavbar';
import * as Utils from '../../components/Utils';

class EditProfile extends Component {
  constructor(props) {
    super(props);
    console.log(1);
    this.state = {
      firstName: '',
      lastName: '',
      year: 'Sophomore',
      major: 'Computer Science',
      gpa: 3.9,
      relevantCourses: ['CS 2110', 'CS 3410', 'INFO 1300'],
      relevantSkills: ['Java', 'Python', 'HTML', 'CSS', 'Javascript', 'Excel'],
      editYear: false,
      editMajor: false,
      editGPA: false,
      editCourses: true,
      editSkills: true,
      editResume: false,
      editTranscript: false,
      invalidYear: false,
      invalidMajor: false,
      invalidGPA: false,
      newCourse: '',
      newSkill: '',
      netId: '',
      resumeId: '',
      transcriptId: '',
      resume: null,
      transcript: null,
      resumeValid: false,
    };
    // this.loadInfoFromServer();
    this.loadInfoFromServer = this.loadInfoFromServer.bind(this);
    this.displayCourses = this.displayCourses.bind(this);
    this.displaySkills = this.displaySkills.bind(this);
  }

  loadInfoFromServer() {
    console.log('Begin loadInfoFromServer');
    axios.get(`/api/undergrads/${sessionStorage.getItem('token_id')}`)
      .then((res) => {
        const info = res.data[0];
        const skills = info.skills === undefined ? [] : info.skills;
        console.log(info.gradYear);
        const year = info.gradYear;
        this.setState({
          relevantCourses: info.courses,
          firstName: info.firstName,
          lastName: info.lastName,
          year,
          major: info.major,
          gpa: info.gpa,
          relevantSkills: skills,
          netId: info.netId,
          resumeId: info.resumeId,
          transcriptId: info.transcriptId,
        });
        console.log('after');
        //     this.setState({relevantCourses: info.courses});
        //     this.setState({firstName: info.firstName});
        //     this.setState({year: utils.gradYearToString(info.gradYear)});
        //     this.setState({major: info.major});
        //     this.setState({gpa: info.gpa});
        //     this.setState({relevantSkills: skills});
        //     this.setState({netId: info.netId});
        //     this.setState({resumeId: info.resumeId});
        //     this.setState({transcriptId: info.transcriptId});
        // this.setState({lastName: info.lastName});
        console.log(this.state.firstName);
        console.log(info);
        if (this.state.firstName === '') {
          console.log("Looks like we didn't load it");
        } else {
          console.log('We did load it :D');
        }
      }).catch((error) => {
        Utils.handleTokenError(error);
      });
  }

  componentDidMount() {
    this.loadInfoFromServer();
    console.log('componentDidMount Working');
  }

  handleChange(event) {
    if (event.target.id === 'year') {
      this.setState({ year: event.target.value });
    } else if (event.target.id === 'major') {
      this.setState({ major: event.target.value });
    } else if (event.target.id === 'gpa') {
      this.setState({ gpa: event.target.value });
    } else if (event.target.id === 'new-course') {
      this.setState({ newCourse: event.target.value });
    } else if (event.target.id === 'new-skill') {
      this.setState({ newSkill: event.target.value });
    }
    // else if (event.target.id === "")
  }

  handleEditUpperBox(event) {
    this.setState({ editYear: !this.state.editYear });
    this.setState({ editMajor: !this.state.editMajor });
  }

  handleEditYear(event) {
    const presentYear = new Date().getFullYear();
    const validateYear = [presentYear + 4, presentYear + 3, presentYear + 2, presentYear + 1, presentYear];
    if (validateYear.indexOf(parseInt(this.state.year, 10)) === -1) {
      this.setState({ invalidYear: true });
    } else {
      this.setState({ invalidYear: false });
      this.setState({ editYear: !this.state.editYear });
    }
  }

  handleEditMajor(event) {
    if (this.state.major === '') {
      this.setState({ invalidMajor: true });
    } else {
      this.setState({ invalidMajor: false });
      this.setState({ editMajor: !this.state.editMajor });
    }
  }

  handleEditResume(event) {
    this.setState({ editResume: !this.state.editResume });
  }

  handleEditTranscript(event) {
    this.setState({ editTranscript: !this.state.editTranscript });
  }

  handleEditGPA(event) {
    if (this.state.gpa === '') {
      this.setState({ invalidGPA: true });
    } else {
      this.setState({ invalidGPA: false });
      this.setState({ editGPA: !this.state.editGPA });
    }
  }
  // Was useful when there was a pencil icon for skills and course. It is commented out if needed
  // handleEditSkills(event) {
  //     this.setState({editSkills: !this.state.editSkills});
  // }

  // handleEditCourses(event) {
  //     this.setState({editCourses: !this.state.editCourses});
  // }

  handleDeleteCourse(data, e) {
    const currentCourses = this.state.relevantCourses;
    const index = currentCourses.indexOf(data);
    currentCourses.splice(index, 1);

    this.setState({ relevantCourses: currentCourses });
  }

  addCourse() {
    if (this.state.newCourse != '') {
      const currentCourses = this.state.relevantCourses;
      currentCourses.push(this.state.newCourse);
      this.setState({ relevantCourses: currentCourses });
      this.setState({ newCourse: '' });
    }
  }

  handleDeleteSkill(data, e) {
    const currentSkills = this.state.relevantSkills;
    const index = currentSkills.indexOf(data);
    currentSkills.splice(index, 1);

    this.setState({ relevantSkills: currentSkills });
  }

  addSkill() {
    if (this.state.newSkill != '') {
      const currentSkills = this.state.relevantSkills;
      currentSkills.push(this.state.newSkill);
      this.setState({ relevantSkills: currentSkills });
      this.setState({ newSkill: '' });
    }
  }

  displayCourses() {
    const list = [];
    if (this.state.editCourses) {
      for (let i = 0; i < this.state.relevantCourses.length; i++) {
        list.push(<div key={i} className="edit-container">
          <div className="editting">
            <p
              className="course editting"
              key={`${this.state.relevantCourses[i]}edit`}
            >
              {this.state.relevantCourses[i]}
            </p>
            <Delete
              size={30}
              id={this.state.relevantCourses[i]}
              onClick={this.handleDeleteCourse.bind(this, this.state.relevantCourses[i])}
              className="delete-icon"
            />
          </div>
        </div>);
      }
      return (
        <div className="display-list">
          <input
            className="addTag"
            onChange={this.handleChange.bind(this)}
            id="new-course"
            type="text"
            name="new-course"
            key="new-course"
            placeholder="Add new course here"
            value={this.state.newCourse}
          />
          <Add className="add-icon" value={this.state.newCourse} size={22} onClick={this.addCourse.bind(this)} />
          {list}
        </div>
      );
    }
    for (let i = 0; i < this.state.relevantCourses.length; i++) {
      list.push(<p
        className="display-list-item course"
        key={this.state.relevantCourses[i]}
      >
        {this.state.relevantCourses[i]}
      </p>);
    }
    return <div className="display-list">{list}</div>;
  }

  displaySkills() {
    const list = [];
    if (this.state.editSkills) {
      for (let i = 0; i < this.state.relevantSkills.length; i++) {
        list.push(<div key={i} className="edit-container">
          <div className="editting">
            <p
              className="skill editting"
              key={`${this.state.relevantSkills[i]}edit`}
            >
              {this.state.relevantSkills[i]}
            </p>
            <Delete
              size={30}
              id={this.state.relevantSkills[i]}
              onClick={this.handleDeleteSkill.bind(this, this.state.relevantSkills[i])}
              className="delete-icon"
            />
          </div>
                  </div>);
      }
      return (
        <div className="display-list">
          <input
            className="addTag"
            onChange={this.handleChange.bind(this)}
            id="new-skill"
            type="text"
            name="new-skill"
            key="new-skill"
            placeholder="Add new skill here"
            value={this.state.newSkill}
          />
          <Add className="add-icon" value={this.state.newSkill} size={22} onClick={this.addSkill.bind(this)} />
          {list}
        </div>
      );
    }

    for (let i = 0; i < this.state.relevantSkills.length; i++) {
      list.push(<p
        className="display-list-item skill"
        key={this.state.relevantSkills[i]}
      >
        {this.state.relevantSkills[i]}
                </p>);
    }
    return <div className="display-list">{list}</div>;
  }

    viewResume = (e) => {
      console.log('We are now viewing the resume');
      e.preventDefault();
      window.location.href = `/doc/${this.state.resumeId}`;
    }

    viewTranscript = (e) => {
      console.log('We are now viewing the transcript');
      e.preventDefault();
      window.location.href = `/doc/${this.state.transcriptId}`;
    }

    onDropResume = (acceptedFiles) => {
      console.log('We are dropping resume');
      acceptedFiles.forEach((file) => {
        const reader = new FileReader();
        reader.onload = (event) => {
          const fileAsBinaryString = reader.result;
          const encodedData = window.btoa(fileAsBinaryString);
          // do whatever you want with the file content
          this.setState({ resume: [encodedData] });
          this.setState({ resumeValid: true });
        };
        reader.onabort = () => console.log('file reading was aborted');
        reader.onerror = () => console.log('file reading has failed');

        reader.readAsBinaryString(file);
      });
      console.log('We have finished dropping resume');
    }

    onDropTranscript = (acceptedFiles) => {
      acceptedFiles.forEach((file) => {
        const reader = new FileReader();
        reader.onload = (event) => {
          const fileAsBinaryString = reader.result;
          const encodedData = window.btoa(fileAsBinaryString);
          // do whatever you want with the file content
          this.setState({ transcript: [encodedData] });
        };
        reader.onabort = () => console.log('file reading was aborted');
        reader.onerror = () => console.log('file reading has failed');

        reader.readAsBinaryString(file);
      });
    }

    onClick = (e) => {
      console.log('Is this working?');
      e.preventDefault();
      const {
        firstName,
        lastName,
        year,
        major,
        gpa,
        relevantCourses,
        relevantSkills,
        editYear,
        editMajor,
        editGPA,
        editCourses,
        editSkills,
        editResume,
        editTranscript,
        invalidYear,
        invalidMajor,
        invalidGPA,
        newCourse,
        newSkill,
        netId,
        resumeId,
        transcriptId,
        resume,
        transcript,
        resumeValid,
      } = this.state;
      const token_id = sessionStorage.getItem('token_id');
      console.log('constant is working');

      if (this.state.resume != null && this.state.resume.length !== 0) {
        axios.post('/api/docs', { token_id, resume })
          .then((result) => {
            console.log('Resume updated, result: ');
            console.log(result);
          }).catch((error) => {
            console.log('Error in posting resume');
            console.log(error);
          });
      }
      if (this.state.transcript != null && this.state.transcript.length !== 0) {
        axios.post('/api/docs', { token_id, transcript })
          .then((result) => {
            console.log('Transcript updated, result: ');
            console.log(result);
          }).catch((error) => {
            console.log('Error in posting transcript');
            console.log(error);
          });
      }
      axios.put(`/api/undergrads/${this.state.netId}`, {
        year, major, gpa, relevantCourses, relevantSkills,
      })
        .then((result) => {
          console.log('undergrad updated, result:');
          console.log(result);
          setTimeout(() => {
            const baseUrl = `${window.location.protocol}//${window.location.host}`;
            window.location.replace(`${baseUrl}/editprofile`);
          }, 1500);

          // access the results here....
        });

      // axios.put('/api/undergrads/' + this.state.netId, {year, major, gpa, relevantCourses, relevantSkills})
      //     .then((result) => {
      //         console.log("undergrad updated, result:");
      //         console.log(result);
      //         //access the results here....
      //     });

      // axios.post('/api/docs', {netId, resume})
      //     .then((result) => {
      //         console.log("Resume updated, result:");
      //         console.log(result);
      //         //access the results here....
      //     });

      // axios.post('/api/docs', {netId, transcript})
      //     .then((result) => {
      //         console.log("Resume updated, result:");
      //         console.log(result);
      //         //access the results here....
      //     });
    };

    render() {
      return (
        <div>
          <Navbar current="editprofile" />
          <div className="profile-page-wrapper">
            <div className="wallpaper" />
            <div className="row edit-row">
              <div className="column edit-details-col">
                <div className=" row title-box-prof">
                  <div className="column left-column">
                    <div className="header">
                      <h4 className="edit-bold">{`${this.state.firstName} ${this.state.lastName}`}</h4>
                    </div>
                    <h6 className="edit-bold">{`${this.state.netId}@cornell.edu`}</h6>
                    {this.state.editYear
                      ? (
                        <div className="input-div">
                            <input
                                className="year edit-input" type="text" name="year" id="year"
                                value={this.state.year} onChange={this.handleChange.bind(this)}
                              />
                            <Check size={25} onClick={this.handleEditYear.bind(this)} className="check-icon" />
                            {this.state.invalidYear ? 'Invalid Year' : ''}
                          </div>
                      )
                      : (
                        <h5>
                            {this.state.year}
                            <Pencil
                                size={20} className="pencil-icon right-column"
                                onClick={this.handleEditYear.bind(this)}
                              />
                          </h5>
                      )

                            }
                    {this.state.editMajor
                      ? (
                        <div className="input-div">
                            <input
                                className="major edit-input" type="text" name="major" id="major"
                                value={this.state.major} onChange={this.handleChange.bind(this)}
                              />
                            <Check size={25} onClick={this.handleEditMajor.bind(this)} className="check-icon" />
                            {this.state.invalidMajor ? 'Required' : ''}
                          </div>
                      )
                      : (
                        <h5>
                            {this.state.major}
                            {' '}
                            <Pencil
                                size={20} className="pencil-icon right-column"
                                onClick={this.handleEditMajor.bind(this)}
                              />
                          </h5>
                      )

                            }

                  </div>
                </div>
              </div>
            </div>


            <div className="edit-row row">
              <div className="qual-box column">
                <div className="edit-row grey-box">
                  <h4>Your Qualifications</h4>
                </div>
                <hr />
                <div className="row qual-row trans-resume">
                  <h5 className="sub-section">Resume:</h5>
                  <input
                    type="button"
                    className="button viewLink"
                    value="View"
                    onClick={this.viewResume}
                  />
                  {this.state.editResume
                    ? (
                      <div className="handleResume">
                        <h5>
                            <Check
                                size={23} className="check-icon"
                                onClick={this.handleEditResume.bind(this)}
                              />
                          </h5>
                        <Dropzone
                            className="edit-drop" style={{
                                position: 'relative',
                                background: '#ededed',
                                padding: '10px',
                                width: '50%',
                                margin: '0 0 0 25%',
                                border: !this.state.resumeValid
                                                                    && this.state.triedSubmitting ? '3px #b31b1b solid' : '1px dashed black',
                              }} onDrop={this.onDropResume.bind(this)}
                          >
                            <p>Click/drag to update resume</p>
                          </Dropzone>
                        <div className="uploaded-message">
                            {this.state.resume != null
                                ? (
                                    <p>
Uploaded:
                                        {this.state.resume.name}
                                      </p>
                                ) : ''}
                          </div>
                      </div>
                    )
                    : (
                      <h5>
                        {' '}
                        <Pencil
                            size={20} className="pencil-icon"
                            onClick={this.handleEditResume.bind(this)}
                          />
                      </h5>
                    )
                                    }
                </div>
                <hr />
                <div className="row qual-row trans-resume">
                  <h5 className="sub-section">Transcript:</h5>
                  <input
                    type="button"
                    className="button viewLink"
                    value="View"
                    onClick={this.viewTranscript}
                  />
                  {this.state.editTranscript
                    ? (
                      <div className="handleTranscript">
                        <h5>
                            <Check
                                size={23} className="check-icon"
                                onClick={this.handleEditTranscript.bind(this)}
                              />
                          </h5>

                        <Dropzone
                            className="edit-drop" style={{
                                position: 'relative',
                                background: '#ededed',
                                padding: '10px',
                                width: '50%',
                                margin: '0 25%',
                                border: '1px dashed black',
                              }} onDrop={this.onDropTranscript.bind(this)}
                          >
                            <p>Click/drag to update transcript</p>

                          </Dropzone>
                        <div className="uploaded-message">
                            {this.state.transcript != null
                                ? (
                                    <p>
Uploaded:
                                        {this.state.transcript.name}
                                      </p>
                                ) : ''}
                          </div>
                      </div>
                    )
                    : (
                      <h5>
                        {' '}
                        <Pencil
                            size={20} className="pencil-icon"
                            onClick={this.handleEditTranscript.bind(this)}
                          />
                      </h5>
                    )
                                    }
                </div>
                <hr />
                <hr />
                <div className="row relevant-row">

                  <div className="column column-49">

                    <h5 className="sub-section">Coursework</h5>

                    {this.displayCourses()}
                  </div>

                  <div className="vl" />

                  <div className="column column-49">

                    <h5 className="sub-section">Skills </h5>

                    {this.displaySkills()}

                  </div>

                </div>
              </div>
            </div>
            <button
              className="column column-50 edit-submit"
              style={{ marginLeft: '45%', marginTop: '10px' }}
              onClick={this.onClick}
            >
Submit
            </button>
          </div>
          <Footer />
        </div>
      );
    }
}

export default EditProfile;
